export const homePageAnim = ()=> {
    const tl = gsap.timeline();
    gsap.to('.hero__image',{
        scrollTrigger: {
          trigger: '.hero',
          start: 'top',
          scrub: true,
        },
        yPercent: -30,
    })
    tl.fromTo('.header__logo',{opacity: 0},{opacity: 1,duration: 1},.2)
    .fromTo('.header__menu li',{opacity: 0},{opacity: 1,stagger: 0.2, duration: 1},.2)
    .fromTo('.hero__title',{ yPercent: 100, opacity: 0 },{ yPercent:0, opacity: 1,duration: 1 },.5)
    .fromTo('.hero__description > p',{ yPercent: 100, opacity: 0 },{ yPercent:0, opacity: 1,duration: 1},1)
    .fromTo('.hero__image',{opacity: 0},{opacity: 1,duration: 1},1.5)
    .fromTo('.hero .marquee',{opacity: 0},{opacity: 1,duration: 1},2)

    // request section animation

    gsap.from('.requests__title h2',{
        scrollTrigger: {
          trigger: '.requests',
          start: '-40% center',
        },
        yPercent: 50,
        opacity: 0,
        duration: 0.5,
    })

    gsap.from('.requests__item',{
        scrollTrigger: {
          trigger: '.requests',
          start: '-20% center',
        },
        yPercent: 50,
        opacity: 0,
        duration: 0.5,
        stagger: 0.1
    })

    gsap.from('.requests__btn',{
        scrollTrigger: {
          trigger: '.requests',
          start: '+20% center',
        },
        yPercent: 50,
        opacity: 0,
        duration: 0.5,
        stagger: 0.1
    })

    //approaches section animation

    gsap.from('.approaches__title h2',{
        scrollTrigger: {
            trigger: '.approaches',
            start: '-40% center',
        },
        yPercent: 50,
        opacity: 0,
        duration: 0.5,
    })

    gsap.from('.approaches__description p',{
        scrollTrigger: {
            trigger: '.approaches',
            start: '-30% center',
        },
        yPercent: 50,
        opacity: 0,
        stagger: .2,
        duration: 0.5,
    })

    // approaches section animation

    const tlApproachesItem = gsap.timeline({
        scrollTrigger: {
            trigger: '.approaches',
            start: 'top center',
        },
    })

    tlApproachesItem.from('.approaches__item:nth-child(1)', {opacity: 0,scale: 1.3})
        .from('.approaches__item:nth-child(2)', {opacity: 0,scale: 1.3,transformOrigin: 'center center'},0.3)
        .from('.approaches__item:nth-child(3)', {opacity: 0,scale: 1.3,transformOrigin: 'center center'},0.9)
        .from('.approaches__item:nth-child(4)', {opacity: 0,scale: 1.3,transformOrigin: 'center center'},0.6)

    gsap.from('.approaches__btn',{
        scrollTrigger: {
            trigger: '.approaches',
            start: '+30% center',
        },
        yPercent: 100,
        opacity: 0,
        duration: 0.5,
    })

    // courses section animation

    gsap.from('.courses__title h2',{
        scrollTrigger: {
            trigger: '.courses',
            start: '-40% center',
        },
        yPercent: 50,
        opacity: 0,
        duration: 0.5,
    })

    gsap.from('.courses__item',{
        scrollTrigger: {
            trigger: '.courses',
            start: '-10% center',
        },
        yPercent: 10,
        scale: 0.7,
        opacity: 0,
        stagger: .3,
        transformOrigin: 'center center',
        duration: 0.5,
    })

    // consultaion section animation

    gsap.from('.consultation',{
        scrollTrigger: {
            trigger: '.consultation',
            start: '-40% center',
        },
        yPercent: 30,
        opacity: 0,
        duration: 0.5,
    })
    gsap.from('.consultation__decoration',{
        scrollTrigger: {
            trigger: '.consultation',
            start: '-50% center',
            end: '100% top',
            scrub: true,
        },
        rotate: 100,
        ease: 'none',
    })
}